@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Unlock&display=swap');
*{
    margin: 0;
    padding: 0;
}
body{
    background-image: url(images/bg4.jpg);
    background-size: cover;
    display: flex;
    justify-content: left;
    align-items: center;
    margin-left: 45px;
    font-family: 'Roboto', sans-serif;
}

.title{
    margin-top: 15px;
    font-size: 75px;
    font-family: "Unlock", serif;
  font-weight: 400;
  font-style: normal;
  color: rgb(118, 25, 9);
  text-shadow: 3px 2px #f80000;
  text-align: center;
}

.Queustion-text{
    margin-top: 25px;
    color: rgb(118, 25, 9);
    font-size: 35px;
    padding-bottom: 15px;
    font-weight: 800;
}

.Answer-section{
    color: rgb(118, 25, 9);
    font-size: 30px;
    line-height: 45px;
    margin: 20px 0;
    padding: 20px;
    border: 2px solid rgb(118, 25, 9);
    border-radius: 10px;
    background-color:antiquewhite;
}
input{
    width: 30px;
    height: 20px;
    margin-right: 10px;
    margin-top: 20px;
}

.next-button{
    padding: 15px 20px;
    border-radius: 20px;
    background-color: rgb(118, 25, 9);
    border: none;
    color: antiquewhite;
    font-size: 25px;
    cursor: pointer;

}

.score-section{
    color: rgb(118, 25, 9);
    font-size: 60px;
    margin: 20px 0;
    padding: 20px;
    text-shadow: 3px 2px #f80000;
    text-align: center;
}
.score-section h3{
    font-size: 50px;
    text-align: center;
}

.restart-button{
   display: flex;
   justify-content: center;
   margin: 25px;
}
.restart-button button{
    padding: 15px 20px;
    border-radius: 20px;
    background-color: rgb(118, 25, 9);
    border: none;
    color: antiquewhite;
    font-size: 25px;
    cursor: pointer;
}

.score-section h2{
    animation: sample 1s infinite;
}

@keyframes sample{


   0% ,100%{
    opacity: 1;
   }
   50%{
    opacity: 0;
   }

}